.Category {
    display: flex;
    align-items: center;
  }
  
  .Category a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    background-color: #ffffff;
    margin: 0 5px;
    transition: background-color 0.3s ease;
  
   
  
    img {
      width: 20px;
      height: 20px;
    }
  }
  
  .Category .category-list {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: scroll;
    scroll-behavior: smooth;
    padding-bottom: 10px;
  
    a {
      flex-shrink: 0;
      margin-right: 10px;
      transition: transform 0.2s ease;
  
      &:hover {
        transform: scale(1.1);
      }
  
      img {
        width: 50%;
        height: 100%;
        border-radius: 8px;
      }
    }
  }