.Login ul{
    display: flex;
    align-items: center;
    gap: 10px;
} 
.local__name{
  cursor: pointer;
  color: black;
  text-decoration: none;
}
.Login ul li{
    color: #4E72CA;
    cursor: pointer;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
} 

.Login-list {
    list-style-type: none;
    display: flex;
    justify-content: center;
  }
  
  .Login-item {
    margin-right: 10px;
  }
  
  .Login-link {
    text-decoration: none;
    color: black;
  }
  
  .Login-registration {
    color: grey;
  }

  .Settings{
    text-decoration: none;
    color: black;
  }