.LoginPage {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  height: 100vh;

  .modal {
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 20px;
    border-radius: 5px;
    text-align: center;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    h4 {
      margin-top: 0;
    }
  }

  // .login__left{
  //   flex: 1;
  // }

  .login__left {
    padding: 20px;
    
    h4 {
      margin-top: 0;
      margin-bottom: 20px;
    }

    p {
      margin-bottom: 20px;
    }

    form {
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;

      .login__input {
        margin-bottom: 10px;
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 5px;

        &.error {
          border-color: red;
        }
      }

      .error {
        color: red;
        margin-top: -10px;
        margin-bottom: 10px;
      }
    }

    .login__button {
      padding: 10px;
      background-color: #007bff;
      color: white;
      border: none;
      border-radius: 5px;
      cursor: pointer;
    }

    span {
      margin-bottom: 10px;
    }

    a {
      text-align: center;
      color: #007bff;
      text-decoration: none;
    }
  }

  .login__right {
    img {
      width: 65%;
     height: 100%;
    }
  }
}