.container {
  max-width: 1140px;
  margin: 0 auto;
  margin-top: 50px;
}

.row {
  display: flex;
  align-items: center;
  gap: 40px;
  padding: 20px 0 50px 0;
}

.col-md-4 {
  width: 100%;
  max-width: 400px;
  margin-bottom: 20px;
}

.img-fluid {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.product-info {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.product-info h3 {
  font-size: 24px;
  margin-bottom: 5px;
}

.product-info p {
  font-size: 20px;
  margin-bottom: 10px;
}

.product-info span {
  font-size: 18px;
  color: #666;
}

.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.spinner {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #007bff;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.product__title{

   font-size: 56px;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   margin-bottom: 25px;
   font-weight: 600;
}
.desc{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 15px;
}

.lead{
  font-weight: bold;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: red;
}



/* Single.css */
.container {
  margin-top: 20px;
}

.product__title {
  font-size: 32px;
  margin-bottom: 10px;
  color: #333;
}

.desc {
  margin-bottom: 20px;
  font-size: 18px;
  color: #555;
}

.order-button {
  margin-top: 12px;
  border-radius: 5px;
  background-color: #f44336;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.order-button:hover {
  background-color: #e53935;
}

.order-confirmation {
  margin-top: 20px;
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 4px;
}

.confirmation-message {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

.confirmation-details {
  font-size: 20px;
  color: #555;
}

.scroll-content {
  margin-top: 40px;
  padding-bottom: 80px; /* Add some bottom padding to prevent content from being hidden under the footer */
}

.scroll-content h3 {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

.scroll-content ul {
  list-style-type: none;
  padding-left: 0;
}

.scroll-content li {
  margin-bottom: 5px;
  color: #555;
}

.scroll-content p {
  margin-bottom: 20px;
  color: #555;
}

.order{
  text-decoration: none;
  color: white;
}