.top__add {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 37px;
}

.order-left {
    display: flex;
    align-items: center;
    border-radius: 22.5px;
    border: 1px solid rgba(151, 151, 151, 0.50);
}

select {
    border: none;
    cursor: pointer;
}

input,
button {
    border: none;
    background-color: transparent;
}

.category__selects {
    height: 100%;
    padding: 0 17px 0 30px;
    border-right: 1px solid rgba(151, 151, 151, 0.50);
}

.category__search {
    border-right: 1px solid rgba(151, 151, 151, 0.50);
    padding: 0 226px 0 25px;
}

.region {
    padding: 0 0 0 24px;

}

.region button {
    margin-left: 18px;
    border-radius: 22px;
    background: #19D476;
    color: #FFF;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    cursor: pointer;
    padding: 13px 19px;
}

.orders__add button {
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 12px;
    border-radius: 22px;
    background: #19D476;
}

.orders__add button span {
    border-radius: 22px;
    background: #11B463;
    padding: 12px 16px;
    flex-shrink: 0;
    color: white;
}

.orders__add h4 {
    color: #FFF;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding-right: 30px;
}

.balansed {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 16px;
    border-bottom: 1px solid silver;

}

.name__account h4 {
    color: #414D6A;
    text-align: center;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.topUp {
    display: flex;
    align-items: center;
    gap: 26px;
}

.topUp h3 {
    color: #304FFF;
    text-align: center;
    font-size: 17px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.topUp button {
    display: flex;
    align-items: center;
    gap: 14px;
    border-radius: 100px 10px 10px 100px;
    border: 1px solid #41D481;
    background: #FFF;
    cursor: pointer;
}

.topUp button span {
    border-radius: 50%;
    padding: 6px 10px;
    background: #11B463;
}

.topUp h5 {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    padding-right: 20px;
}

.announcement__catalog {
    border-bottom: 1px solid rgba(151, 151, 151, 0.60);
}

.announcement__catalog ul {
    display: flex;
    align-items: center;
    gap: 65px;

}

.announcement__catalog ul li {
    color: #414D6A;
    font-size: 17px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    cursor: pointer;
    padding: 15px 0;
}
.announcement__catalog ul li:nth-child(1) {
    color: #304FFF;
}

.announcement__catalog ul li:hover {
    color: #304FFF;
    transition: .3s all;
    font-size: 18px;
    border-bottom: 2px solid #304FFF;
}