*{
    font-family: Arial, sans-serif;
}
.order-confirmation {

  background-color: #f1f1f1;
  padding: 20px;
}

.order-form {
  margin-bottom: 20px;
}


.form-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.form-group {
  margin-bottom: 15px;
}

.form-label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.form-input {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.error {
  color: red;
  font-size: 14px;
  margin-top: 5px;
}

.error-message {
  margin-top: 10px;
}

.order-details {

  padding: 10px;
  background-color: #fff;
}

.confirmation-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

.confirmation-message {
  margin-bottom: 10px;
}

.confirmation-id {
  font-weight: bold;
  color: #555;
}

.countdown-timer {
  font-size: 18px;
  color: #555;
  margin-bottom: 20px;
}

.submit-button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #45a049;
}

