* {
    padding: 0;
    margin: 0;
}
.active {
    display: block;
  }
  .Footer {
    /* Your footer styles */
    
    .footer__item {
      h4 {
        cursor: pointer;
      }
      h4:hover {
        transition: .5s all;
        color: #05F3B3;
      }
  
      p {
        display: none;
        /* Your styles for hidden <p> elements */

      }
  
      p.active {
        display: block;
        /* Your styles for visible <p> elements */
      }
    }
  }
p {
    padding: 0;
}

.footer__block {
    padding: 52px 0 30px 0;
    max-width: 1140px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.footer__list {
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
    padding-bottom: 50px;
}

.footer__item {
    width: 246px;
}

.footer__item h4 {
    color: #FFF;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 20px;
}

.footer__item p {
    color: #878F99;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    cursor: pointer;
}

.footer__bottom ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #4C565E;
    padding-top: 30px;
}


.footer__bottom ul li:hover {
    transition: .5s all;
    color: #05F3B3;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 15px;
    font-style: normal;
    line-height: normal;
    cursor: pointer;
}

.footer__bottom ul li {
    color: #66717A;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    cursor: pointer;
}